.youtube{
    background: #efefef;

    figure{
      width: 100%;
      height: 100%;
      position: relative; 
      margin-top: -100px;
    //   background-image: url(../../../public/img/youtube01.jpg);
        // background-image: url(../../../public/img/youtube01.jpg);
        background:#1e2831;
        // background-size: cover;
        // background-position: center bottom;
        // background-repeat: no-repeat
        // ;
        // filter: saturate(50%);
        overflow: hidden;

        .explanation01{
            position: absolute;
            left: 1450px;
            top: 10.5%;
            width: 200px;
            height: 200px;

            .exP{
                font:normal 10px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 1.2px;
                color:#fff;
                width: 200px;
                height: 100%;
                display: block;
                position: absolute;
                transform: rotate(90deg);

            &::before{
                content: '';
                display: block;
                width: 2px;
                height: 80%;
                background: #fff;
                position: absolute;
                bottom:45%;
                right:20%;
                transform: rotate(90deg);
                }

            &::after{
                content: '';
                display: block;
                font-size: 20px;
                color:#fff;
                position: absolute;
                bottom:78.5%;
                right:-22%;
                transform: rotate(270deg);
                }
            }
        }

        .youtubeContent{
            width: 1180px;
            height: 100%;
            margin: 0 auto;
            display: flex;
            justify-content: space-evenly;
            align-items: flex-start;
            padding:20px 75px;
            margin-top: 200px;
            
            .box{
                border-top:3px solid #fff;
                border-left:3px solid #fff;
                border-bottom:3px solid #fff;
                width: 320px;
                height: 370px;
            }

            h1{
                font:normal 75px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
                position: absolute;
                left: 38.5%;
                transform: translateX(-50%);
                top:245px;

                &::before{
                content: '';
                display: block;
                width: 3px;
                height: 35px;
                background: #fff;
                position: absolute;
                top: -7.5%;
                right:10.5%;
                }

                &::after{
                content: '';
                display: block;
                width: 3px;
                height: 35px;
                background: #fff;
                position: absolute;
                bottom: -7.5%;
                right:10.5%;
                }
    
                span{
                    display: block;
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
    
                b{
                    color:#fff;
                }
            } 
    
            p{
                font:normal 11px/20px 'Orbitron';
                color:#ddd;
                letter-spacing: 1.3px;
                margin-bottom: 30px;
                padding-top:55px;
            }
        }
    
        .inner{
            width: 1180px;
            position: relative;
            margin: 0 auto;
            height: 100%;
            padding:800px 15px 35px 15px;

            h2{
                font:bold 50px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 5px;
                text-align: center;
                margin-bottom: 50px;
            }

            >section{
                padding-bottom: 100px;
                width: 1180px;

                &::after{
                    content: '';
                    display: block;
                    clear: both;
                }

                article{
                    width: 100%;
                    height: 400px;
                    float: left;
                    padding:10px;
                    margin-bottom: 100px;

                    &:nth-child(1){
                        position: absolute;
                        top: 125px;
                        left: -200px;
                        width: 70%;
                        height: 550px;
                        transition: 1s;

                        &:hover{
                            transform: translateY(-10px);
                            .pic{
                                opacity: 1;
                            }
                        }

                        .inner{
                            padding:50px;
                            background: rgba(0,0,0,.3);

                            .pic{
                                width: 60%;
                                height: 80%;
                                top: 10%;
                                left: 5%;

                                img{
                                    width: 100%;
                                    height: 100%;

                                }
                            }

                            .txt{
                                top: 25%;
                            }

                            .subContent{
                                top: 65%;
                            }
                        }
                    }
                    
                    &:nth-child(2){
                        position: absolute;
                        top: 140px;
                        left: 750px;
                        width: 50%;
                        height: 255px;
                        transition: 1s;

                        &:hover{
                            transform: translateY(-10px);
                            .pic{
                                opacity: 1;
                            }
                        }

                        .inner{
                            padding:50px;
                            background: rgba(0,0,0,.3);

                            .pic{
                                width: 45%;
                                height: 80%;
                                top: 10%;
                                left:5%;

                                img{
                                    width: 100%;
                                    height: 100%;

                                }
                            }

                            .txt{
                                top: 30%;
                                
                                h3{
                                    font:bold 15px/1 'Orbitron';
                                }

                                p{
                                    overflow: hidden;
                                    text-overflow: ellipsis;   
                                    display: -webkit-box;
                                    -webkit-line-clamp: 1; 
                                    -webkit-box-orient: vertical;
                                    padding-top: 10px;
                                }
                            }

                            .subContent{
                                top: 60%;

                                >p{
                                    overflow: hidden;
                                    text-overflow: ellipsis;   
                                    display: -webkit-box;
                                    -webkit-line-clamp: 2; 
                                    -webkit-box-orient: vertical;
                                }
                            }
                        }
                    }

                    &:nth-child(3){
                        position: absolute;
                        top: 410px;
                        left: 750px;
                        width: 50%;
                        height: 255px;
                        transition: 1s;

                        &:hover{
                            transform: translateY(-10px);
                            .pic{
                                opacity: 1;
                            }
                        }

                        .inner{
                            padding:50px;
                            background: rgba(0,0,0,.3);
                            
                            .pic{
                                width: 45%;
                                height: 80%;
                                top: 10%;
                                left:5%;

                                img{
                                    width: 100%;
                                    height: 100%;

                                }
                            }

                            .txt{
                                top: 30%;
                                
                                h3{
                                    font:bold 15px/1 'Orbitron';
                                }

                                p{
                                    overflow: hidden;
                                    text-overflow: ellipsis;   
                                    display: -webkit-box;
                                    -webkit-line-clamp: 1; 
                                    -webkit-box-orient: vertical;
                                    padding-top: 10px;
                                }
                            }

                            .subContent{
                                top: 60%;

                                >p{
                                    overflow: hidden;
                                    text-overflow: ellipsis;   
                                    display: -webkit-box;
                                    -webkit-line-clamp: 2; 
                                    -webkit-box-orient: vertical;
                                }
                            }
                        }
                        
                    }
                    
                    .inner{
                        width: 100%;
                        height: 100%;
                        background: rgba(0,0,0,.1);
                        box-shadow: 3px 3px 10px rgba(0,0,0,.7);
                        padding:0px;

                        /*
                        word-break :    
                        - normal :   ,     (default)
                        - break-all :      
                        - keep-all : ,     
                        */

                        .pic{
                            position: absolute;
                            width: 40%;
                            height: 380px;
                            cursor: pointer;
                            overflow: hidden;
                            margin-bottom: 20px;
                            left: 30px;
                            top:-30px;
                            opacity: 0.5;
                            transition: 1s;
                            
                            &:hover{
                                opacity: 1;
                                transform: translateY(-10px);
                            }

                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                transform: scale(1.34);
                            }
                        }

                        > .txt{
                            width: 50%;
                            position: absolute;
                            right: 30px;
                            top: 30px;
                            
                            h3{
                                position: absolute;
                                top:-40px;
                                font:bold 20px/1 'Orbitron';
                                color:rgba(255,255,255,.7);
                                letter-spacing: 2.3px;
                            }

                            p{
                                font:normal 10px/15px 'Orbitron';
                                color:#bbb;
                                letter-spacing: 1.2px;
                                padding-top: 25px;
                            }
                        }

                        .subContent{
                            position: absolute;
                            width: 50%;
                            right: 30px;
                            top: 55%;
                            transform: translateY(-50%);

                            >p{
                                font:normal 10px/22px 'Orbitron';
                                color:#ccc;
                            }

                            span{
                                font:bold 10px/1 'Orbitron';
                                color:#fff;
                                letter-spacing: 2.3px;
                                text-align: right;
                                display: block;
                            }
                        }
                    }
                }
            }
        }
        
        >img{
            object-fit: contain;
            position: absolute;
            z-index:1;
            bottom: -100%;

            &:nth-of-type(1){
                left: 10%;
                width: 90px;
                height: 90px;
                animation: locationAni01 30s linear infinite;
            }
    
            &:nth-of-type(2){
                left: 25%;
                width: 100px;
                height: 100px;
                animation: locationAni02 30s  5s linear infinite;
            }
    
            &:nth-of-type(3){
                left: 50%;
                width: 75px;
                height: 75px;
                animation: locationAni03 40s 7s linear infinite;
            }
    
            &:nth-of-type(4){
                left: 75%;
                width: 65px;
                height: 65px;
                animation: locationAni04 35s  4s linear infinite;
            }

            &:nth-of-type(5){
                left: 90%;
                width: 120px;
                height: 120px;
                animation: locationAni05 30s 2s linear infinite;
            }
        }
    }
}

.pop{
    width: 100%;
    height: 90%;
    background: #000;
    position: fixed;
    top: 5%;
    left: 0;
    padding:50px;

    iframe{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    span{
        position: absolute;
        color:#fff;
        font:bold 13px/1 'arial';
        top: 20px;
        right: 20px;
        cursor: pointer;
    }
}

@keyframes locationAni01{
    0%{bottom: -200%; left: 10%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.5;}
    50%{opacity: 0.5; }
    75%{bottom: 100%; left: 25%; opacity: 0;}
    100%{bottom:0%;   opacity: 0;}
}

@keyframes locationAni02{
    0%{bottom: -200%; left: 25%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.1;}
    50%{opacity: 0.1;}
    75%{bottom: 80%;  left: 50%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni03{
    0%{bottom: -200%; left: 50%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.2;}
    50%{opacity: 0.2; }
    75%{bottom: 120%; left: 35%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni04{
    0%{bottom: -200%; left: 75%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.4;}
    50%{opacity: 0.4; }
    75%{bottom: 100%; left: 60%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni05{
    0%{bottom: -200%; left: 90%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.3;}
    50%{opacity: 0.3;}
    75%{bottom: 70%;  left: 80%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}


