#board{
    width: 100%;
    padding: 250px 0px 150px;
    background-image: url(../../../public/img/infoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    filter: saturate(70%);
    
    .custom-shape-divider-top-1646763706 {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        transform: rotate(180deg);
        z-index:-1;
    }
    
    .custom-shape-divider-top-1646763706 svg {
        position: relative;
        display: block;
        width: calc(100% + 1.3px);
        height:200px;
    }
    
    .custom-shape-divider-top-1646763706 .shape-fill {
        fill: #23323b;
        opacity: 0.3;
    }

    .explanation11{
        position: absolute;
        left: 150px;
        bottom: 100px;
        width: 200px;
        height: 200px;

        .exP11{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '';
              display: block;
              font-size: 20px;
              color:rgba(255,255,255,.5);
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }


    .inner{
        width: 1180px;
        margin: 0 auto;
        // border:4px solid yellow;

        strong{
            position: absolute;
            top:5%;
            left:17.7%;
            font:bold 250px/1 'Orbitron';
            color:rgba(255,255,255,.07);
            letter-spacing: 7px;
            transition: 2.5s;
        }

        >h2{
            font:bold 85px/1 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 7px;
            text-align: center;
            margin-bottom: 30px;
            text-align: center;
            transition: 2.5s;
        }

        ul{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: flex-start;
            // border: 3px solid red;
            padding:100px 0px 50px;

            li{
                width: 48%;
                height: 200px;
                padding: 40px;
                box-shadow: 3px 3px 3px rgba(0,0,0,.9);
                margin-bottom: 20px;
                // background: #1E2831;
                background: #23323b;
                cursor: pointer;

                h2{
                    font:bold 18px/1 'Orbitron';
                    letter-spacing: 2px;
                    color:rgba(255,255,255,.9);
                    margin-bottom: 10px;
                }

                p{
                    font:normal 12px/2.3 'Orbitron';
                    letter-spacing: 2.3px;
                    color:rgba(255,255,255,.7);
                }
            }
        }
    }

    .custom-shape-divider-bottom-1646764312 {
        position: absolute;
        bottom: -100px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        z-index:-1;
    }
    
    .custom-shape-divider-bottom-1646764312 svg {
        position: relative;
        display: block;
        width: calc(134% + 1.3px);
        height: 300px;
    }
    
    .custom-shape-divider-bottom-1646764312 .shape-fill {
        fill: #23323b;
        opacity: 0.6;
    }
}


@media screen and(max-width:1559px) {
    #board{
        padding: 200px 0px;
    
        .explanation11{left: 175px;}
    
    
        .inner{
            padding:0px 40px 0px 20px;
    
            strong{
                top:10%;
                left:17.7%;
                font:bold 150px/1 'Orbitron';
            }
    
            >h2{
                font:bold 65px/1 'Orbitron';
                letter-spacing: 7px;
                margin-bottom: 0px;
            }
    
            ul{
                padding:50px 0px 100px;
    
                li{
                    h2{font:bold 16px/1 'Orbitron';}
    
                    p{font:normal 11px/2.3 'Orbitron';}
                }
            }
        }
    }
}

@media screen and(max-width:$tablet) { 
    #board{
        padding: 200px 0px;
    
        .explanation11{
            left:50px;
      
            .exP11{
                letter-spacing: 1.3px;
                color:rgba(255,255,255,.4);
    
            &::before{
                height: 65%;
                bottom:55%;
                right:100.3%;
                background: rgba(255,255,255,.3);
                }
      
              &::after{
                  bottom:80.5%;
                  right:128%;
                  }
              }
          }
    
    
        .inner{
            width: 100%;
            padding:0px 20px;

            strong{
                left:12.7%;
                font:bold 125px/1 'Orbitron';
            }
    
            >h2{
                font:bold 50px/1 'Orbitron';
                position: absolute;
                left: 20%;
            }
    
            ul{
                padding:100px 0px;
    
                li{
                    h2{font:bold 15px/1 'Orbitron';}
                }
            }
        }
    }
}

