#pics{
    width: 100%;
    height: 100%;
    background-image: url(../../../public/img/storyBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding-top:150px;
    padding-bottom: 150px;
    overflow: hidden;
    
    >strong{
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.01);
        letter-spacing: 10px;
        position: absolute;
        top: 2%;
        right:-2%;
        text-align: right;

        b{
            display: inline-block;
            position: relative;
            color:rgba(255,255,255,.02);
            text-shadow: 0px 0px 3px rgba(0,255,255,.01);

            &:nth-child(1){
                &::before{
                    content: 'PHOTOS';
                    position: absolute;
                    font:bold 10px/1 'Orbitron';
                    color:rgba(255,255,255,.09);
                    letter-spacing: 3px;
                    display: block;
                    top: -5px;
                    left: 47.5%;
                    transform: translateX(-50%);
                }
            }

            &:nth-child(2){
                &::before{
                    content: 'TOP10';
                    position: absolute;
                    font:bold 10px/1 'Orbitron';
                    color:rgba(255,255,255,.09);
                    letter-spacing: 3px;
                    display: block;
                    top: -5px;
                    left: 47.5%;
                    transform: translateX(-50%);
                }
            }
        }
    }

    h2{
        font:bold 85px/1 'Orbitron';
        color:rgba(255,255,255,.8);
        text-shadow: 0px 0px 3px rgba(255,255,255,.1);
        letter-spacing: 10px;
        position: absolute;
        top: 12.5%;
        left: 1.5%;
    }

    >p{
        font:normal 13px/1 'Orbitron';
        color:rgba(255,255,255,.6);
        letter-spacing: 3.3px;
        position: absolute;
        top: 18.5%;
        left: 1.5%;
    }

    .svg-inline--fa{
        font-size: 150px;
        color:rgba(0,0,0,.06);
        position: absolute;
        left: 50%;
        top: 53.75%;
        transform: translate(-50%,-50%) rotate(-10deg);
    }

    .warp{
        height: 100%;
        width: 100%;

        ul{
            width: 100%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            margin: 0 auto;
            justify-content: space-around;
            align-items: center;
            perspective: 700px;
            padding:300px 0px 200px;

            li{
                width: calc((100% - 100px) / 5);
                height: 400px;
                display: flex;
                flex-direction: column;
                border:0.5px solid rgba(255,255,255,.1);
                padding:25px;
                position: relative;
                margin-bottom: 100px;
                background: rgba(255,255,255,.02);
                cursor: pointer;
                box-shadow: 3px 3px 5px rgba(0,0,0,.9);
                transform-style: preserve-3d;
                top:-3000px;
                transition:1s;

                &:hover{
                    animation-play-state: paused;
                    box-shadow: 0px 0px 10px rgba(0, 255, 255, 0.15);
                    background: rgba(255,255,255,.07);
                    transform:translateY(-50px);

                    img{
                        filter: saturate(100%);
                        opacity: 0.9;
                        box-shadow: 3px 3px 5px rgba(255, 255, 255, 0.1);
                    }
    
                    strong{
                        opacity: 1;
                        top: -35px;
                    }
    
                    h3{
                        color:rgba(255,255,255,.3);
                        text-shadow: 0px 0px 1px rgba(255,255,255,.9);
                    }
    
                    p,span{
                        text-shadow: 0px 0px 1px rgba(255,255,255,.9);
                    }
                }

                &:nth-child(n+1){
                    animation:picsAni01 25s linear;
                    animation-fill-mode: forwards;

                    &:nth-child(1){animation-delay:0s}
                    &:nth-child(2){animation-delay:0.2s}
                    &:nth-child(3){animation-delay:0.4s}
                    &:nth-child(4){animation-delay:0.6s}
                    &:nth-child(5){animation-delay:0.8s}
                }

                &:nth-child(n+6){
                    margin-bottom: 0px;
                    animation:picsAni02 25s linear;
                    animation-fill-mode: forwards;

                    &:nth-child(6){animation-delay:1.2s}
                    &:nth-child(7){animation-delay:0.9s}
                    &:nth-child(8){animation-delay:0.6s}
                    &:nth-child(9){animation-delay:0.3s}
                    &:nth-child(10){animation-delay:0s}
                }

                img{
                    width: 100%;
                    height: 275px;
                    object-fit: cover;
                    margin-bottom: 10px;
                    filter: saturate(70%);
                    opacity: 0.6;
                    box-shadow: 3px 3px 5px rgba(0,0,0,.9);
                    transition:1s;
                }

                strong{
                    overflow: hidden;
                    text-overflow: ellipsis;   
                    display: -webkit-box;
                    -webkit-line-clamp: 1; 
                    -webkit-box-orient: vertical;
                    position: absolute;
                    top: 0px;
                    left: 50%;
                    opacity: 0;
                    transform: translateX(-50%);
                    font:bold 14px/1 'Orbitron';
                    color:lightcyan;
                    text-shadow: 0px 0px 5px aqua;
                    letter-spacing: 5px;
                    transition:1s;
                }

                h3{
                    overflow: hidden;
                    text-overflow: ellipsis;   
                    display: -webkit-box;
                    -webkit-line-clamp: 1; 
                    -webkit-box-orient: vertical;
                    font:bold 18px/1 'Orbitron';
                    transform: rotate(90deg);
                    color:rgba(255,255,255,.2);
                    position: absolute;
                    right: -80px;
                    top: 37.5%;
                    width: 70%;
                    letter-spacing: 3px;
                    transition:1s;
                }

                p{
                    overflow: hidden;
                    text-overflow: ellipsis;   
                    display: -webkit-box;
                    -webkit-line-clamp: 2; 
                    -webkit-box-orient: vertical;
                    font:bold 10px/15px 'Orbitron';
                    letter-spacing: 2.3px;
                    color:rgba(255,255,255,.6);
                    margin-top: 15px;
                    margin-bottom: 12.5px;
                    transition:1s;
                }

                span{
                    font:bold 10px/1 'Orbitron';
                    letter-spacing: 2.3px;
                    color:rgba(255,255,255,.8);
                    transition:1s;
                    margin-bottom: -15px;
                }
            }
        }
    }

    .custom-shape-divider-bottom-1646247006 {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
    }

    .custom-shape-divider-bottom-1646247006 svg {
        position: relative;
        display: block;
        width: calc(100% + 1.3px);
        height: 250px;
    }

    .custom-shape-divider-bottom-1646247006 .shape-fill {
        fill: #23323b;
        opacity: 0;
    }

    .custom-shape-divider-top-1646267501 {
        position: absolute;
        bottom: 350px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        // border:3px solid yellow;
        z-index:-1;
    }
    
    .custom-shape-divider-top-1646267501 svg {
        position: relative;
        display: block;
        width: calc(127% + 1.3px);
        height: 500px;
        transform: rotateY(180deg) rotateZ(-180deg);
    }
    
    .custom-shape-divider-top-1646267501 .shape-fill {
        fill: #23323b;
        opacity: 0.15;
    }
}

@keyframes picsAni01{
    0%{top:-3000px; opacity: 0;}
    5%{top:0px; left:100%; opacity: 0;}
    30%{ top:-200px; }
    80%{top:200px; opacity: 1;}
    85%{ opacity: 0; left:-100%;  top:0px;}
    90%{ opacity: 0; left:0px; top:-100px;} 
    100%{top:0px; left:0px; opacity: 1; transition:2s;}
}

@keyframes picsAni02{
    0%{top:-3000px; opacity: 0;}
    5%{top:0px; left:-100%; opacity: 0;}
    30%{top:-150px;}
    80%{ top:250px; opacity: 1;}
    85%{ opacity: 0; left:100%; top:0px;}
    90%{ opacity: 0; left:0px; top:100px;}
    100%{top:0px; left:0px; opacity: 1; transition:2s;}
}

@keyframes picsAni03{
    0%{top:-3000px; opacity: 0;}
    5%{top:0px; left:-100%; opacity: 0;}
    30%{top:-150px;}
    80%{ top:250px; opacity: 1;}
    85%{ opacity: 0; left:100%; top:0px;}
    90%{ opacity: 0; left:0px; top:-50px;}
    100%{top:0px; left:0px; opacity: 1; transition:2s;}
}

@keyframes picsAni04{
    0%{top:-100px; opacity: 0;}
    100%{top:0px; opacity: 1; transition:2s;}
}


