.login{
    margin-top:-100px;
    height: 100vh;
    padding:100px 0px 150px;
    position: relative;
    // overflow: hidden;
    // background-image: url(../../../public/img/login02.jpg);
         // background-image: url(../../../public/img/youtube01.jpg);
         background:#1e2831;
         // background-size: cover;
         // background-position: center bottom;
         // background-repeat: no-repeat
         // ;
         // filter: saturate(50%);
         overflow: hidden;

    .inner{ 
        width: 500px;
        height: 100%;
        margin: 0 auto;

        .loginForm{
            background: rgba(0,0,0,.2);
            padding-bottom: 0;
            box-shadow: 5px 5px 10px rgba(0,0,0,.9);

            h1{
                font:bold 40px/1 'Orbitron';
                color:#f9bf7cc9;
                letter-spacing: 5px;
                text-align: center;
                position: absolute;
                top: 25%;
                left: 50%;
                transform: translateX(-50%);
            }

            b{
                font:normal 10px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 2px;
                margin-bottom: 20px;
                display: block;
                text-align: center;
            }

            button{
                font:bold 10px/1 'Orbitron';
                color:rgba(0,0,0,.8);
                letter-spacing: 3px;
                width: 70px;
                height: 25px;
                cursor: pointer;
                transition: 0.5s;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                margin-bottom: 50px;
                border:none;

                &:hover{
                    background: rgba(0,0,0,.5);
                    color:rgba(255,255,255,.8);
                }
            }
            
            table{
                width: 100%;
                margin: 0 auto;

                tbody{
                    width: 500px;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                    padding:150px 0px 25px;
                }
            
                th{
                    width: 100%;
                    height:30px;
                    font:bold 11px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 3px;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-bottom: 10px;

                    &.btnSet{
                        text-align: center;
                        margin-top: 10px;
                        padding:15px 0px 30px;

                        input[type=reset], input[type=submit]{
                            width: 120px;
                            height: 30px;
                            text-align: center;
                            margin: 20px;
                            cursor: pointer;
                            transition: 0.5s;
                            border:none;
                            background: #f9bf7c;
                            font:bold 11px/13px 'Orbitron';
                            color:#333;
                            letter-spacing: 1.5px;
            
                            &:hover{
                                background: #111;
                                color:#fff;
                            }
                        }
                    }
                }

                td{
                    width: 100%;
                    
                    input[type=text], input[type=password],select{
                        width: 100%;
                        padding:10px 20px;
                        border:1px solid #aaa;
                        margin-bottom: 30px;
                    }
        
                    label{
                        font:12px/1 'arial';
                        color:#777;
                        margin-right: 5px;
                    }
            
                    .err{
                        font:bold 10px/13px 'Orbitron';
                        color:hotpink;
                        display: block;
                        margin-top:-10px;
                        margin-bottom: 15px;
                        text-align: center;
                        letter-spacing: 1.3px;
                    }
                }
            }        
        }
    }
}


