footer{
    width:100%;
    padding:50px 0px;
    background-image: url(../../../public/img/mapBg01.jpg);
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    height: 120px;
    filter: saturate(70%);
    border-top:0.5px solid rgba(255,255,255,.09);

    .inner{
        padding:0px 30px;
        width:100%;
        margin:0px auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        h1{
            font:bold 20px/1 'Orbitron';
            color:#eee;
            letter-spacing: 2px;
            width: 25%;
        }

        ul{
            width: 50%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin:0px 100px;

            li{
                font:normal 15px/1 'Orbitron';
                color:#ccc;
                letter-spacing: 2px;
            }
        }

        ol{
            width: 25%;
            display: flex;
            justify-content: space-evenly;
            align-items: center;

            li{
                font:bold 15px/1 'Orbitron';
                color:#eee;
                letter-spacing: 2px;
            }
        }
    }
}



@media screen and(max-width:1559px) {

    footer{
    
        .inner{
    
            ul{
                margin:0px 125px 0px 25px;
    
                li{
                    font:normal 13px/1 'Orbitron';
                    margin-right: 15px;

                    &:last-child{
                       margin-right: 0px;;
                    }
                }
            }
        }
    }
}

@media screen and (max-width : $tablet){
    footer{

        .inner{
            padding:0px 3vw;
            width:100%;

            h1{font:bold 14px/1 'Orbitron';}

            ul{
                margin:0px 135px 0px 15px;
    
                li{
                    font:normal 10px/1 'Orbitron';
                }
            }

            ol{
    
                li{font:bold 10px/1 'Orbitron';}
            }
        }
    }    
}

@media screen and (max-width: 899px){
    footer{
        padding:30px 0px 65px;

        .inner{
            padding:0px 5vw;
            width:100%;

            h1{font:bold 16px/1 'Orbitron';}

            ul{
                position: absolute;
                bottom: 25px;
                margin:0px 135px 0px 15px;
    
                li{
                    font:normal 10px/1 'Orbitron';
                }
            }

            ol{
    
                li{font:bold 11px/1 'Orbitron';}
            }
        }
    }    
}

@media screen and (max-width: 679px){
    footer{
        padding:50px 0px;

        .inner{

            ul{display: none;}
        }
    }    
}

