.location{

    figure{
        width: 100%;
        height: 100%;
        position: relative; 
        margin-top: -100px;
        // background-image: url(../../../public/img/locationBg02.jpg);
        // background-image: url(../../../public/img/youtube01.jpg);
        background:#1e2831;
        // background-size: cover;
        // background-position: center bottom;
        // background-repeat: no-repeat
        // ;
        // filter: saturate(50%);
        overflow: hidden;

        .explanation04{
            position: absolute;
            left: 150px;
            top: 17.5%;
            width: 200px;
            height: 200px;
      
            .exP4{
                font:normal 10px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 1.2px;
                color:#fff;
                width: 200px;
                height: 100%;
                display: block;
                position: absolute;
                transform: rotate(-90deg);
      
            &::before{
                content: '';
                display: block;
                width: 2px;
                height: 80%;
                background: #fff;
                position: absolute;
                bottom:45%;
                right:90%;
                transform: rotate(90deg);
                }
      
              &::after{
                  content: '';
                  display: block;
                  font-size: 20px;
                  color:#fff;
                  position: absolute;
                  bottom:78%;
                  right:125%;
                  transform: rotate(-270deg);
                  }
              }
          }


        .contact{
            width: 1180px;
            margin: 0 auto;
            margin-top: 200px;
            padding:50px 0px;
            margin-bottom: 150px;
            
            h2{
                font:bold 50px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 5px;
                text-align: center;
                margin-bottom:30px;
                position: relative;

                &::before{
                    content: '';
                    display: block;
                    position: absolute;
                    top: -20px;
                    right: 30%;
                    width: 25%;
                    height: 2px;
                    background: #fff;
                }
            }

            >p{
                font:normal 10px/20px 'Orbitron';
                color:#999;
                letter-spacing: 1.3px;
                text-align: center;
                width: 60%;
                margin: 0 auto;
                margin-bottom: 50px;
            }
            
            .snsList{
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                width: 30%;
                margin: 0 auto;
                margin-bottom: 50px;

                li{
                    font-size: 25px;
                    color:rgba(255,255,255,.8);
                    border-radius: 100px;
                    padding:10px 15px;
                    background: rgba(0,0,0,.8);
                    transition: 0.5s;
                    
                    &:hover{
                        color:#fff;
                        background: #000;
                    }

                }
            }

            >article{
                width: 100%;

                >ul{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    li{
                        padding:10px;
                        width: 33.333%;
                        display: flex;
                        flex-direction: column;
                        background: rgba(255,255,255,.03);
                        box-shadow: 5px 5px 10px rgba(0,0,0,.9);
                        justify-content: space-evenly;
                        height: 300px;
                        margin: 0px 30px;
                        padding-left: 30px;
                        transition: 1s;

                        &:hover{
                            transform: translateY(-10px);
                            box-shadow: 5px 5px 3px rgba(255,255,255,.2);
                            font-weight: bold;
                        }
                    
                        .svg-inline--fa{
                            width: 35px;
                            font-size: 30px;
                            color:rgba(0,0,0,.9);
                            border-radius: 100px;
                            padding:17.5px;
                            background: rgba(255,255,255,.9);
                            margin-bottom: 10px;
                        }

                        strong{
                            font:bold 20px/1 'Orbitron';
                            color:rgba(255,255,255,.8);
                            letter-spacing: 3px;
                            margin-bottom: 30px;
                        }

                        span{
                            font:normal 10px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing: 1.2px;
                        }
                    }
                }
            }
        }

        .inner{
            position:relative;
            top: 10%;
            width: 100%;
            height: 100vh;
            padding:100px 0px;
            z-index:2;
            
            h1{
                position: absolute;
                top: 0%;
                left: 2.5%;
                font:bold 110px/1 'Orbitron';
                color:rgba(255,255,255,.6);
                letter-spacing: 10px;
            }
    
            >section{
                position: relative;
                height: 100%;
                width: 100%;

                #map{
                    width: 100%;
                    height: 600px;
                    background: #000;
                    filter:saturate(5%);
                    border:none;
                    position: absolute;
                    top: 10%;
                    left: 50%;
                    transform: translate(-50%);
                    opacity: 0.7;
    
                    img{
                        object-fit: contain;
                    }
                }

                .traffic{
                    position: absolute;
                    right: 2.5%;
                    top: -5%;
                    width: 400px;
                    height: 75px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    >button{
                        width: 47.5%;
                        height: 100%;
                        border:none;
                        background: #fff;
                        box-shadow: 3px 3px 3px rgba(255,255,255,.5);
                        cursor: pointer;
                        font:bold 10px/1 'Orbitron';
                        color:#333;
                        letter-spacing: 1.3px;
                        position: relative;
                        transition:all 1s;

                        b{
                            display: block;
                            margin-bottom: 10px;
                            font-size: 12px;
                            color:#f9bf7c;
                        }

                        &:hover{
                            transform: translateY(-25px);
    
                            &::after{
                                content: '';
                                display: block;
                                height: 5px;
                                position: absolute;
                                background: #f9bf7c;
                                top: -15px;
                                right: 50%;
                                animation: locationAni 1s;
                                animation-fill-mode: forwards;
                            }
    
                            &::before{
                                content: '';
                                display: block;
                                height: 5px;
                                position: absolute;
                                background: #f9bf7c;
                                top: -15px;
                                left:50%;
                                animation: locationAni 1s;
                                animation-fill-mode: forwards;
                            }
                        }
                    }
                }
    
                .branch{
                    width: 1180px;
                    position:absolute;
                    bottom: 0;
                    left:50%;
                    transform: translateX(-50%);
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
    
                    button{
                        width: 25%;
                        height: 100px;
                        border:none;
                        background: #fff;
                        box-shadow: 3px 3px 5px rgba(255,255,255,.5);
                        font:bold 12px/1 'Orbitron';
                        color:#f9bf7c;
                        letter-spacing: 1.5px;
                        transition:all 1s;
                        cursor: pointer;
                        position: relative;
    
                        &.on{
    
                        }
    
                        &:hover{
                            transform: translateY(-25px);
    
                            &::after{
                                content: '';
                                display: block;
                                height: 5px;
                                position: absolute;
                                background: #f9bf7c;
                                bottom: -15px;
                                right: 50%;
                                animation: locationAni 1s;
                                animation-fill-mode: forwards;
                            }
    
                            &::before{
                                content: '';
                                display: block;
                                height: 5px;
                                position: absolute;
                                background: #f9bf7c;
                                bottom: -15px;
                                left:50%;
                                animation: locationAni 1s;
                                animation-fill-mode: forwards;
                            }
                        }
    
                        p{
                            font:normal 10px/1 'Orbitron';
                            color:#777;
                            letter-spacing: 1px;
                            margin: 15px 0;
                        }
    
                        span{
                            font:bold 10px/1 'Orbitron';
                            color:#333;
                            letter-spacing: 1.8px;
                        }
                        
                    }
                }
            }
        }

        .wayHere{
            width: 100%;
            height: 100%;
            padding:0px 100px 100px;
            margin-bottom: 150px;

            ul{
                display: flex;
                justify-content: space-between;
                align-items: center;

                li{
                    border:0.5px solid rgba(255,255,255,.7);
                    padding:35px 50px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin-right: 50px;

                    &:nth-child(4){
                        margin-right: 0px;
                    }

                    &:hover{
                        
                        .svg-inline--fa{
                            box-shadow: 3px 3px 5px rgba(255,255,255,.3);
                            color:#fff;
                            animation: locationAni08 1s linear;
                        }
                    }

                    .svg-inline--fa{
                        width: 50px;
                        height: 50px;
                        border:1px solid rgba(255,255,255,.5);
                        padding:20px;
                        font-size:30px;
                        color:rgba(255,255,255,.9);
                        transition: 1s;
                        border-radius:50%;
                    }

                    strong{
                        font:bold 15px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 1.5px;
                        margin: 30px 0px 15px;
                    }

                    span{
                        font:bold 18px/1 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 2.3px;
                        margin-bottom: 15px;
                    }

                    p{
                        font:normal 11px/1 'Orbitron';
                        color:rgba(255,255,255,.6);
                        letter-spacing: 1.8px;
                        text-align: center;
                    }
                }
            }
        }


        .custom-shape-divider-top-1645429182 {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            overflow: hidden;
            line-height: 0;
            transform: rotate(180deg);
            opacity: 0.5;
        }
        
        .custom-shape-divider-top-1645429182 svg {
            position: relative;
            display: block;
            width: calc(107% + 1.3px);
            height: 150px;
        }
        
        .custom-shape-divider-top-1645429182 .shape-fill {
            fill: #1E2C4980;
        }

        >img{
            object-fit: contain;
            position: absolute;
            z-index:1;
            bottom: -100%;

            &:nth-of-type(1){
                left: 10%;
                width: 90px;
                height: 90px;
                animation: locationAni01 30s linear infinite;
            }
    
            &:nth-of-type(2){
                left: 25%;
                width: 100px;
                height: 100px;
                animation: locationAni02 30s  5s linear infinite;
            }
    
            &:nth-of-type(3){
                left: 50%;
                width: 75px;
                height: 75px;
                animation: locationAni03 40s 7s linear infinite;
            }
    
            &:nth-of-type(4){
                left: 75%;
                width: 65px;
                height: 65px;
                animation: locationAni04 35s  4s linear infinite;
            }

            &:nth-of-type(5){
                left: 90%;
                width: 120px;
                height: 120px;
                animation: locationAni05 30s 2s linear infinite;
            }

            &:nth-of-type(6){
                left: 0%;
                width: 400px;
                height: 400px;
                bottom: 600px;
                opacity: 1;
                transform: rotate(90deg);
                animation: locationAni06 12s linear infinite;
            }

            &:nth-of-type(7){
                left: 0%;
                width: 300px;
                height: 300px;
                bottom: 600px;
                opacity: 1;
                transform: rotate(90deg);
                animation: locationAni07 12s linear infinite;
            }
        }
    }
}

@keyframes locationAni{
    0%{width: 0%;}
    100%{width: 40%;}
}

@keyframes locationAni01{
    0%{bottom: -200%; left: 10%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.5;}
    50%{opacity: 0.5; }
    75%{bottom: 100%; left: 25%; opacity: 0;}
    100%{bottom:0%;   opacity: 0;}
}

@keyframes locationAni02{
    0%{bottom: -200%; left: 25%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.1;}
    50%{opacity: 0.1;}
    75%{bottom: 80%;  left: 50%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni03{
    0%{bottom: -200%; left: 50%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.2;}
    50%{opacity: 0.2; }
    75%{bottom: 120%; left: 35%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni04{
    0%{bottom: -200%; left: 75%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.4;}
    50%{opacity: 0.4; }
    75%{bottom: 100%; left: 60%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni05{
    0%{bottom: -200%; left: 90%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.3;}
    50%{opacity: 0.3;}
    75%{bottom: 70%;  left: 80%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni06{
    0%{bottom: 1350px; left: 0%; opacity: 0;}
    25%{opacity: 0.3;}
    75%{bottom:1450px;  left: 80%; opacity: 0;}
    100%{bottom: 1350px; opacity: 0;}
}

@keyframes locationAni07{
    0%{bottom: 1350px; left: 0%; opacity: 0;}
    25%{opacity: 0.3;}
    75%{bottom:1420px;  left: 75%; opacity: 0;}
    100%{bottom:1350px; opacity: 0;}
}

@keyframes locationAni08{
    0%{transform: rotate(0deg);}
    100%{transform: rotate(360deg);}
}


