    95%{right:40%; bottom:80%; opacity: 0;}
    100%{right:50%; bottom:0%; opacity: 0;}
}

@keyframes joinAni9 {
    0%{right:50%; bottom:0%; opacity: 0;}
    5%{opacity: 0.5}
    95%{right:42%; bottom:80%; opacity: 0;}
    100%{right:50%; bottom:0%; opacity: 0;}
}

