#about{
    width: 100%;
    height: 100vh;
    padding: 150px 0px 0px;
    margin-top: -100vh;
    position: relative;
    overflow: hidden;

    >strong{
        font:bold 250px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 3px;
        position: absolute;
        transform: rotate(270deg);
        left:-200px;
        top: 25%;  
        opacity:0; 
        transition: 1.5s;
    }

    .explanation06{
        position: absolute;
        right:255px;
        bottom: 50px;
        width: 200px;
        height: 200px;

        .exP6{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(90deg);

        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '';
            display: block;
            font-size: 20px;
            color:rgba(255,255,255,.5);
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    .inner{
        width: 100%;
        margin: 0 auto;

        .wrap{
            width: 1180px;
            height: 100%;
            margin: 0 auto;
            // border:1px solid yellow;
            padding:50px 250px;

            h2{
                font:normal 100px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 5px;
                line-height: 130px;
                margin-bottom: 20px;
                b{
                    font-size:130px;
                }
            }

            >p{
                position: relative;
                left: 62.5%;
                transform: translateX(-70%);
                font:normal 10px/17.5px 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 1.2px;
                width: 80%;

                &:nth-child(3){
                    margin-top: 30px;
                    margin-bottom: 30px;
                }
            }
        }
    }

    .page02{
        margin-left: 30px;
        position: absolute;
        bottom: 1%;

        strong{
            font:bold 15px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            letter-spacing: 3px;
        }
    }
}


@media screen and(max-width:1559px) {

    #about{
        padding: 150px 20px;
    
        >strong{
            font:bold 200px/1 'Orbitron';
            left:-150px;
        }
    
        .explanation06{right:175px;}
    
        .inner{
    
            .wrap{
                padding:50px 250px 50px 275px;
    
                h2{
                    font:normal 80px/1 'Orbitron';

                    b{
                        font-size:105px;
                        line-height:70px;
                    }
                }
    
                >p{left: 65%;}
            }
        }
    
        .page02{
            margin-left: 0px;
    
            strong{font:bold 13px/1 'Orbitron';}
        }
    }
}

@media screen and(max-width:$tablet) { 

    #about{

        >strong{
            font:bold 175px/1 'Orbitron';
            left:-130px;
        }
    
        .explanation06{
            right:35px;

            .exP6{
                color:rgba(255,255,255,.4);

            &::before{
                height: 65%;
                bottom:55%;
                right:20%;
                background: rgba(255,255,255,.3);
                }
    
            &::after{
                bottom:81%;
                right:-14%;
                }
            }
        }
    
        .inner{
    
            .wrap{
                padding:50px 290px 50px 235px;
    
                h2{
                    font:normal 65px/1 'Orbitron';

                    b{
                        font-size:90px;
                        line-height:50px;
                    }
                }
    
                >p{left: 65%;}
            }
        }
    
        .page02{
            margin-left: 0px;
    
            strong{font:bold 13px/1 'Orbitron';}
        }
    }
}

@media screen and (max-width: 899px){
    #about{
        .explanation06{bottom: 75px;}

        >strong{
            font:bold 140px/1 'Orbitron';
            left:-105px;
        }

        .inner{
    
            .wrap{
                width: 100%;
                padding:100px 30px 50px 205px;

                h2{
                    font:normal 60px/1 'Orbitron';

                    b{font-size:85px;}
                }
    
                >p{
                    overflow: hidden;
                    text-overflow: ellipsis;   
                    display: -webkit-box;
                    -webkit-line-clamp: 5; 
                    -webkit-box-orient: vertical;
                }
            }
        }
    }
}

@media screen and (max-width: 679px){

    #about{
        .explanation06{display: none;}

        >strong{
            font:bold 140px/1 'Orbitron';
            left:-105px;
        }

        .inner{
    
            .wrap{
                padding:150px 0px 50px 175px;

                >p{-webkit-line-clamp: 6; }
            }
        }
    }
}

@media screen and (max-width: $mobile){ 
    #about{
        padding:0px 20px;

        >strong{
            font:bold 120px/1 'Orbitron';
            left:-95px;
            top: 40%;
        }

        .inner{
    
            .wrap{
                padding:100px 0px 50px 30px;

                h2{
                    font:normal 50px/1 'Orbitron';

                    b{font-size:75px;}
                }
            }
        }
    }
}