@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

#story{
    width: 100%;
    height: 100%;
    background-image: url(../../../public/img/storyBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding-top:100px;
    padding-bottom:275px;
    position: relative;
    overflow: hidden;
    
    .explanation08{
        position: absolute;
        right:200px;
        bottom:50px;
        width: 200px;
        height: 200px;

        .exP8{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(90deg);

        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '';
            display: block;
            font-size: 20px;
            color:rgba(255,255,255,.5);
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    >strong{
        font:bold 100px/1 'Orbitron';
        color:rgba(255,255,255,.7);
        text-shadow: 0px 0px 3px rgba(255,255,255,.1);
        letter-spacing: 10px;
        position: absolute;
        transform: rotate(270deg);
        left:-100px;
        top: 40%;
        transition: 1s;
        opacity: 0;
    }

    .inner{
        width: 1180px;
        height: 100%;
        margin: 0 auto;

        >.svg-inline--fa{
            font-size: 50px;
            color:#eee;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            z-index:10;
            top: 120px;
            opacity: 0.9;
        }

        .wrap{
            width: 100%;
            height: 100%;

            .storyGroup{
                width: 100%;
                height: 600px;
                display: flex;
                overflow: hidden;
                transition: 1.5s;

                &.on{
                    height: 1050px;
                }


                .storyPic{
                    width: 50%;
                    height: 1000px;

                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        filter: saturate(50%);
                    }
                }
        
                .storyContent{
                    height: 1000px;
                    width: 50%;
                    padding:75px 50px;

                    h3{
                        font:bold 30px/35px 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 5px;
                        margin-bottom: 35px;
                    }

                    p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 1.7px;
                        margin-bottom: 50px;
                    }

                    strong{
                        font:bold 22.5px/25px 'Dancing Script';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 1.3px;
                        width: 50%;
                        display: block;
                        margin-bottom: 50px;
                    }

                    span{
                        font:bold 10px/1 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 3px;
                        text-align: right;
                        display: block;
                    }

                    .fa-angle-down{
                        position: relative;
                        font-size: 50px;
                        top: -50.5%;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        cursor: pointer;
                        animation: angleAni 10s;
                        animation-fill-mode: forwards;

                        &:hover{
                            opacity: 1;
                        }

                        &.off{
                            opacity: 0;
                            display: none;
                        }
                    }

                    .fa-angle-up{
                        position: relative;
                        font-size: 50px;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        opacity: 0;
                        display: none;
                        cursor: pointer;
                        transition: 3s;

                        &:hover{
                            opacity: 1;
                        }

                        &.on{
                            opacity: 0.8;
                            display: block;
                        }
                    }
                }
            }
        }
    }
}

@keyframes angleAni {
    0%{opacity: 0;}
    100%{opacity: 0.8;}
}


@media screen and(max-width:1559px) {

    #story{
    
        .explanation08{right:50px;}
    
        >strong{
            font:bold 75px/1 'Orbitron';
        }
    
        .inner{
            padding:0px 50px 20px 150px;
    
            >.svg-inline--fa{
                font-size: 45px;
                left: 55%;
            }
    
            .wrap{
    
                .storyGroup{
                    height: 630px;
    
                    &.on{
                        height: 950px;
                    }
            
                    .storyContent{
    
                        h3{font:bold 21px/35px 'Orbitron';}
    
                        p{
                            font:normal 10px/20px 'Orbitron';
                            letter-spacing: 1.4px;

                            &:nth-child(4){
                                overflow: hidden;
                                text-overflow: ellipsis;   
                                display: -webkit-box;
                                -webkit-line-clamp: 11; 
                                -webkit-box-orient: vertical;
                            }
                        }
    
                        strong{
                            font:bold 18.5px/23px 'Dancing Script';
                            letter-spacing: 1px;
                        }
    
                        .fa-angle-down{
                            top: -37.5%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and(max-width:$tablet) { 

    #story{
    
        .explanation08{
            right:25px;

            .exP8{
                color:rgba(255,255,255,.4);

            &::before{
                height: 65%;
                bottom:55%;
                right:20%;
                background: rgba(255,255,255,.3);
                }
    
            &::after{
                bottom:81%;
                right:-14%;
                }
            }
        }
    
        .inner{
            width: 100%;
            padding:0px 20px 20px 125px;
    
            >.svg-inline--fa{
                font-size: 45px;
                left: 56%;
            }
    
            .wrap{
    
                .storyGroup{
            
                    .storyContent{
                        padding:75px 10px 75px 30px;

                        h3{
                            font:bold 17px/30px 'Orbitron';
                        }
    
                        p{
                            font:normal 10px/18px 'Orbitron';
                            margin-bottom: 40px;

                            &:nth-child(4){-webkit-line-clamp: 13; }
                        }
    
                        strong{
                            font:bold 17.5px/20px 'Dancing Script';
                            letter-spacing: 2px;
                            width: 65%;
                        }
    
                        .fa-angle-down{
                            top: -38%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 899px){

    #story{
        padding-top:200px;

        >strong{
            font:bold 75px/1 'Orbitron';
            transform: rotate(0deg);
            top: 50px;
            left:50%;
            color:rgba(255,255,255,.5);
        }

        .inner{
            padding:0px 20px 20px 20px;
    
            >.svg-inline--fa{
                left: 50%;
                top:220px;
            }
            
            .wrap{
    
                .storyGroup{
                    height: 500px;

                    &.on{
                        height: 800px;
                    }
            
                    .storyContent{

                        h3{
                            font:bold 14px/25px 'Orbitron';
                        }
    
                        p{
                            font:normal 10px/16px 'Orbitron';
                            margin-bottom: 30px;
                            overflow: hidden;
                            text-overflow: ellipsis;   
                            display: -webkit-box;
                            -webkit-line-clamp: 13; 
                            -webkit-box-orient: vertical;
                        }
    
                        strong{
                            font:bold 15.5px/20px 'Dancing Script';
                            width: 75%;
                        }
    
                        .fa-angle-down{
                            top: -35%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 679px){

    #story{
        padding-bottom: 150px;

        >strong{left:35%;}

        .explanation08{display: none;}

        .inner{
            >.svg-inline--fa{font-size: 35px;}
            
            .wrap{

                .storyGroup{
                    height: 495px;

            
                    .storyContent{

                        h3{
                            font:bold 12px/25px 'Orbitron';
                        }
    
                        p{
                            font:normal 10px/17px 'Orbitron';
                            -webkit-line-clamp: 11; 
                        }
    
                        strong{
                            font:bold 13.5px/20px 'Dancing Script';
                            width: 90%;
                            margin-bottom: 30px;
                        }
    
                        .fa-angle-down{
                            top: -34.5%;
                            font-size: 35px;
                        }

                        .fa-angle-up{
                            bottom: -10px;
                            font-size: 35px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile){ 

    #story{

        >strong{
            left:30%;
            font:bold 50px/1 'Orbitron';
        }

        .inner{

            >.svg-inline--fa{
                left: 10%;
                top:43%;
                transform: rotate(3deg);
            }
            
            .wrap{

                .storyGroup{
                    height: 580px;
                    flex-direction: column;

                    &.on{
                        height: 850px;
                    }

                    .storyPic{
                        width: 100%;
                        height: 40%;
                    }
            
                    .storyContent{
                        width: 100%;
                        height: 60%;
                        padding:30px 20px;

                        h3{
                            font:bold 10px/25px 'Orbitron';
                            margin-bottom: 20px;
                        }
    
                        p{
                            font:normal 10px/18px 'Orbitron';
                            -webkit-line-clamp: 8; 
                            margin-bottom: 30px;

                            &:nth-child(4){
                                -webkit-line-clamp: 5; 
                            }
                        }
    
                        strong{
                            font:bold 12px/20px 'Dancing Script';
                            width: 100%;
                            margin-bottom: 30px;
                        }
    
                        .fa-angle-down{top: -54%;}

                        .fa-angle-up{bottom: 0px;}
                    }
                }
            }
        }
    }
}