#visualFigure{
    width: 100%;
    height: 200vh;
    position: relative;
    background-image: url(../../../public/img/visualBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    overflow: hidden;

    >img{
        position: absolute;
        width: 300px;
        height: 300px;
        object-fit: cover;
        top: 25%;
        left: 62.5%;
        transition: all 3s;
    }

    .explanation05{
        position: absolute;
        left: 275px;
        top: 42.5%;
        width: 200px;
        height: 200px;
  
        .exP5{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '';
              display: block;
              font-size: 20px;
              color:rgba(255,255,255,.5);
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }

    >section{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        // border:3px solid blue;

        >.inner{
            width: 1180px;
            height: 100%;
            margin: 0 auto;
            position: relative;

            h2{
                font:bold 35px/1 'Orbitron';
                color:rgba(255,255,255,.3);
                letter-spacing: 1.3px;
                position: absolute;
                top:30%;
                left: -5%;
                animation: visualAni 1s linear;
                animation-fill-mode: forwards;

                &:nth-child(1){
                    top: 27%;
                }

                &:nth-child(2){
                    left: 0;
                }
            }

            h3{
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 5px;
                position: absolute;
                top:33%;
                left: 0;
                animation: visualAni2 1s linear;
                animation-fill-mode: forwards;
            }

            p{
                font:normal 13px/1 'Orbitron';
                color:rgba(255,255,255,.6);
                letter-spacing: 2.3px;
                position: absolute;
                top:40%;
                left: 30%;
                animation: visualAni2 1s linear;
                animation-fill-mode: forwards;
            }

            b{
                font:bold 13px/1 'Orbitron';
                color:rgba(255,255,255,.6);
                letter-spacing: 2.3px;
                position: absolute;
                top:41.5%;
                left: 30%;
                animation: visualAni2 1s linear;
                animation-fill-mode: forwards;
                
                span{
                    font-weight: normal;
                }
            }
        }

        .page01{
            position: absolute;
            top: 49%;
            transform: translateY(-50%);
            left: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            width: 100%;
            padding:0px 30px;

            strong{
                font:bold 15px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 3px;
            }

            ul{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width:150px;
                li{
                    cursor: pointer;
                    font:bold 15px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 3px;
                }
            }
        }
    }
    // .swiper{
    //     height: 100%;

    //     .swiper-slide{
    //         // background: aqua;
    //         width: 100%;
    //         display: flex;
    //         justify-content: center;
    //         align-items: center;
    //         font-size: 10vw;
    //         color:#fff;
    //     }
    // }

}

@keyframes visualAni{
    0%{transform: translateX(-100%); opacity: 0;}
    100%{transform: translateX(0); opacity: 1;}
}

@keyframes visualAni2{
    0%{transform: translateX(100%); opacity: 0;}
    100%{transform: translateX(0); opacity: 1;}
}


