                h2{
                    font:bold 30px/1 'Orbitron';
                    left: 50px;
    
                    &:nth-child(2){
                        left: 100px;
                    }
                }
    
                h3{
                    font:bold 75px/1 'Orbitron';
                    left: 100px;
                }
    
                p{
                    top:38.5%;
                    left: 365px;
                }
    
                b{
                    top:40%;
                    left: 365px;
                }
            }
    
            .page01{
                padding:0px 20px;

                strong{font:bold 13px/1 'Orbitron';}
    
                ul{
                    li{font:bold 13px/1 'Orbitron';}
                }
            }
        }
    }
}

