@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');

.gallery {

    figure{
      width: 100%;
      height: 100%;
      position: relative; 
      margin-top: -100px;
      // background-image: url(../../../public/img/gallery06.jpg);
        // background-image: url(../../../public/img/youtube01.jpg);
        background:#1e2831;
        // background-size: cover;
        // background-position: center bottom;
        // background-repeat: no-repeat
        // ;
        // filter: saturate(50%);
        overflow: hidden;

      .galleryMian{
        position: relative;
        margin: 0 auto;
        width: 1180px;
        padding:50px;
        height:100%;
        box-shadow: 5px 5px 10px rgba(0,0,0,.7);
        margin-top: 150px;
  
        video{
          width: 100%;
          height: 100%;
          object-fit: cover;
          filter: saturate(30%);
        }
  
        
      .explanation02{
        position: absolute;
        left: -250px;
        top: 55%;
        width: 200px;
        height: 200px;
  
        .exP2{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: #fff;
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '';
              display: block;
              font-size: 20px;
              color:#fff;
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }
  
        .galleryContent{
          width: 45%;
          position: absolute;
          top: 50px;
          padding:50px 25px;
  
          h1{
            font:bold 75px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            text-shadow: 5px 5px 10px rgba(255,255,255,.2);
            letter-spacing: 5px;
            margin-bottom: 50px;
            position: relative;
  
            &::before{
              content: '';
              display: block;
              width: 90%;
              height: 2.5px;
              background: rgba(255,255,255,.7);
              position: absolute;
              bottom: -15px;
              right: 30%;
            }
          }
      
          p{
            font:normal 10px/15px 'Orbitron';
            color:rgba(255,255,255,.3);
            letter-spacing: 1px;
          }
    
            
          strong{
            font:bold 10px/1 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            margin-top: 30px;
            display: block;
          }
    
        }
  
      }
    
      .picList{
        width: 1180px;
        margin: 0 auto;
        padding:250px 0px;
        position: relative;
        height:100%;
  
        .pics{
          display: flex;
          width: 1180px;
          height: 100%;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          h2{
            position: absolute;
            top: 125px;
            right: -25%;
            font:bold 100px/1 'Orbitron';
            color:rgba(255,255,255,.1);
            letter-spacing: 5px;
          }
  
          .pic{
            width: 100%;
            height: 100%;
            margin-bottom: 75px;
            display: flex;
  
            .picture{
              width: 50%;
              height: 100%;
              position: relative;
  
            strong{
              font:bold 100px/1 'Orbitron';
              color:rgba(255,255,255,.5);
              letter-spacing: 5px;
            }
  
              img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                filter: saturate(30%);
                padding:0px 50px 50px;
                transition: 1s;
  
                &:hover{
                  filter: saturate(100%);
                }
              }
    
              .box1{
                width:85%;
                height: 70%;
                border:0.5px solid rgba(255,255,255,.7);
                position: absolute;
                bottom:27.5px;
                left: 62.5px;
                z-index:-1;
              }
    
            }
  
            .content{
              width: 50%;
              height: 100%;
              padding:115px 50px 65px;
              display: flex;
              flex-direction: column;

              h4{
                font:bold 20px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 3.5px;
                margin-bottom: 30px;
              }
  
              p{
                font:normal 10px/20px 'Orbitron';
                color:rgba(255,255,255,.5);
                letter-spacing: 1.7px;
                margin-bottom: 40px;
              }
  
              b{
                font:bold 11px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 2.3px;
                margin-bottom: 50px;
                display: block;
                text-align: left;
                position: relative;
  
                &::after{
                  content: '';
                  display: block;
                  width: 30px;
                  height: 1px;
                  background: #fff;
                  left: 200px;
                  position: absolute;
                  top: 50%;
                  transform: translateY(-50%);
                }
              }
  
              button{
                font:normal 10px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 2.3px;
                width: 130px;
                height: 30px;
                padding:10px;
                background: none;
                cursor: pointer;
                border:1px solid #fff;
                transition: 1s;
  
                &:hover{
                  color:rgba(0,0,0,.9);
                  border:1px solid #aaa;
                  background: rgba(255,255,255,.9);
                  font-weight: bold;
                }
              }
  
              span{
                font:bold 10px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 2.3px;
                text-align: right;
                display: block;
              }
            }
  
            &:nth-child(2n+1){
              display: flex;
              flex-direction: row-reverse;
  
              .picture{
                strong{
                  text-align: right;
                  display: block;
                }
              }
            }
          }
        }
      }

    .jellyfish{
      width: 300px;
      height: 250px;
      object-fit: cover;
      position: absolute;
      top: 40.5%;
      left: 0%;
      opacity: 0.3;
      transform: rotate(-30deg);
      z-index:3;
    }

    .dolphin{
      width: 300px;
      height: 250px;
      object-fit: cover;
      position: absolute;
      top: 51%;
      right: -5%;
      opacity: 0.5;
    }

        
    .explanation03{
      position: absolute;
      right: 7.5%;
      bottom: 35%;
      width: 200px;
      height: 200px;

      .exP3{
          font:normal 10px 'Orbitron';
          color:rgba(255,255,255,.8);
          letter-spacing: 1.2px;
          color:#fff;
          width: 200px;
          height: 100%;
          display: block;
          position: absolute;
          transform: rotate(90deg);

          &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: #fff;
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '';
            display: block;
            font-size: 20px;
            color:#fff;
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    h3{
      font:bold 100px/1 'Orbitron';
      color:rgba(255,255,255,.09);
      letter-spacing: 5px;
      position: relative;
      top:-75px;
      left: 5%;
    }

    .innerWrap {
      transform: translateY(200px);
      opacity: 0;
      transition: 1s;
      position: relative;
      width: 100%;
      bottom: 50px;
      height: 100%;

      >.inner {
        transition: none;
        width: 1180px;
        margin: 0px auto;
        position: relative;
        border:1px solid rgba(0,0,0, .01);
        height: 100%;
    

        .searchBox{
          position: absolute;
          top: 25px;
          right: 10px;

          input[type=text]{
            width: 167px;
            height: 35px;
            font-family: 'Orbitron';
            font-size: 10px;
            padding-left:5px;
            letter-spacing: 1.2px;
          }

          button{
            width: 50px;
            height: 35px;
            font-family: 'Orbitron';
            color:#fff;
            font-size: 10px;
            background: rgba(0,0,0,.5);
            border:none;
            cursor: pointer;
            letter-spacing: 1.2px;
          }
        }

        .loadingAni{
          position: relative;
          top: 250px;
      
          img{
            position: absolute;
            perspective: 800;
            opacity: 0;
      
            &:nth-child(1){
              width:300px;
              height: 200px;
              transform: rotateY(-180deg);
              position: absolute;
              left: 250px;
              animation: galleryAni01 10s;
            }
            
            &:nth-child(2){
              width:10px;
              height: 10px;
              animation: galleryAni02 9s 1s;
            }
      
            &:nth-child(3){
              width:15px;
              height:15px;
              animation: galleryAni03 7s 3s;
              animation-delay: 3s;
            }
          }
        }

        >section {
          transform: translateY(200px);
          opacity: 0;
          transition: 1s;
          margin-top: 75px;
          margin-bottom:50px;
    
          &.on {
            transform: translateY(0px);
            opacity: 1;
          }
    
          &::after {
            content: '';
            display: block;
            clear: both;
          }
    
          article {
            width: 20%;
            height: 250px;
            float: left;
            padding: 10px;
    
            >.inner {
              width: 100%;
              height: 100%;
              background: #fff;
              box-shadow: 5px 5px 10px rgba(0,0,0, .1);
              padding: 10px;
    
              .pic {
                width: 100%;
                height: 80%;
                margin-bottom: 10px;
                cursor: pointer;
    
                img {
                  width: 100%; 
                  height: 100%;
                }            
              }
    
              >b {
                width: 100%;
                height: 20%;
                display: block;
                font:normal 10px/1 'Orbitron';
                color:#555;
                letter-spacing: 2.5px;
                margin-bottom: 20px;
                text-overflow: ellipsis;   
                overflow: hidden;
                -webkit-box-orient: vertical;
                white-space:nowrap;
              }
            }
          }
        }
      } 
    } 
  
      &.on {
        .innerWrap {
          transform: translateY(0px); opacity: 1;
        }
      }
    }
  }
  
  .popup {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 5vw;
    z-index: 6;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  
    span {
      font: bold 14px/1 'arial';
      color: #fff;
      position: absolute;
      top: 4vh;
      right: 5vw; cursor: pointer;
    }
  }
  

  @keyframes galleryAni01{
    0%{left:250px; opacity: 0;}
    10%{opacity: 0.5;}
    90%{ opacity: 0.5;}
    100%{ left: 500px; opacity: 0;}
  }

  @keyframes galleryAni02{
    0%{left: 485px; top:65px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left: 700px; top:-10px; opacity: 0;}
  }

  @keyframes galleryAni03{
    0%{left: 590px; top:70px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left:700px; top:20px;opacity: 0;}
  }

 
  @media screen and(max-width:1559px) {

    .gallery {

        figure{

          .galleryMian{
            width: 90%;
      
          .explanation02{display: none;}
      
            .galleryContent{
              padding:30px 50px;

              p{
                overflow: hidden;
                text-overflow: ellipsis;   
                display: -webkit-box;
                -webkit-line-clamp: 9; 
                -webkit-box-orient: vertical;
              }
            }
          }
            
          .picList{
            width: 90%;
      
            .pics{
              width: 100%;

              h2{
                right: -5%;
                font:bold 80px/1 'Orbitron';
              }
      
              .pic{
      
                .picture{

                strong{font:bold 80px/1 'Orbitron';}
                }
      
                .content{
      
                  b{margin-bottom: 20px;}
      
                  button{width: 120px;}
      
                  span{
                    font:bold 10px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 2.3px;
                    text-align: right;
                    display: block;
                  }
                }
              }
            }
          }

          .jellyfish{
            width: 250px;
            height: 200px;
            top: 39%;
          }
        
          .dolphin{
            width: 250px;
            height: 200px;
            top: 54%;
            right: -7%;
          }
        
              
          .explanation03{display: none;}
        
          h3{
            font:bold 80px/1 'Orbitron';
            top:-50px;
            left: 2.5%;
          }
    
          .innerWrap {

            >.inner {
              width: 100%;

              .searchBox{
                top: 25px;
                right: 0px;
      
                input[type=text]{width: 160px;}
              }

              >section {
          
                article {
          
                  >.inner {
          
                    >b {letter-spacing: 1.8px;}
                  }
                }
              }
            } 
          } 
        }
      }
}

