#video{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/videoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 150px 0px 0px;
    position: relative;
    filter: saturate(70%);
    z-index:10;

    .explanation07{
    position: absolute;
    right: 27.5%;
    bottom:-5%;
    width: 200px;
    height: 200px;
    z-index:10;

    .exP7{
        font:normal 10px 'Orbitron';
        color:rgba(255,255,255,.5);
        letter-spacing: 2px;
        color:#fff;
        width: 200px;
        height: 100%;
        display: block;
        position: absolute;
        transform: rotate(-90deg);

    &::before{
        content: '';
        display: block;
        width: 2px;
        height: 80%;
        background: rgba(255,255,255,.5);
        position: absolute;
        bottom:45%;
        right:90%;
        transform: rotate(90deg);
        }

      &::after{
          content: '';
          display: block;
          font-size: 20px;
          color:rgba(255,255,255,.5);
          position: absolute;
          bottom:78%;
          right:125%;
          transform: rotate(-270deg);
          }
      }
  }


    >strong{
        font:bold 250px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 3px;
        position: absolute;
        right: 50px;
        top:-150px;
        transition: 1s;
        opacity: 0;

        b{
          font-size: 300px;
          line-height:200px;
        }
        
    }

    .inner{
        width: 100%;
        margin: 0 auto;

        #navi{
            width: 35%;
            height: 100%;
            position: absolute;
            top: 0px;
            right: 0px;
            // background: rgba(0,0,0,.2); backdrop-filter: blur(5px); 
            z-index: 4;
            overflow: hidden;

            h3{
              font:bold 100px/1 'Orbitron';
              color:rgba(255,255,255,.8);
              letter-spacing: 7px;
              position: absolute;
              left: 65%;
              top: 15%;
              transform: translateX(-50%);
            }

            ul{
                width: 100%;
                position: absolute;
                top: 65%;
                left: 0px;
                transform: translateY(-50%);

                li {
                    width: 100%;
                    padding: 50px 100px;
                    position: relative;
                    overflow: hidden;
                    opacity: 0.4;
                    cursor: pointer;
                    transition: 1.4s;
                    transform-origin: left center;

                    h2 {
                        font: bold 12px/1 'orbitron';
                        color: #fff;
                        letter-spacing: 1px;
                        margin-bottom: 5px;

                        &::after {
                            content: '';
                            display: block;
                            width: 20px;
                            height: 1px;
                            background: #fff;
                            margin-top: 5px;
                            transition: 1.4s;
                        }
                    }
                    p {
                        font: 10px/1 'orbitron';
                        color: #fff;
                        letter-spacing: 0.5px;
                        opacity: 0.7;
                    }

                    &.on {
                            background: linear-gradient(to right, rgba(0,0,0,.4), rgba(0,0,0,0));
                            transform: scale(1.3); opacity: 1;

                        h2::after {
                            width: 150px;
                        }
                    }
                }
            }
        }

        .masks{
            width: 75%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;

            >div{
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0px;
                left: 0px;
                z-index:1;

                video{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transform: rotateY(180deg);
                    filter: saturate(15%);
                }

                .slogan {
                    position: absolute;
                    right: 8%;
                    top: 50%;
                    transform: translateY(-60%);
                    text-align: right; padding: 50px;
                    perspective: 700px;
              
                    &::before {
                        content: '';
                        display: block;
                        width: 500px;
                        height: 500px;
                        border: 1px solid #fff;
                        position: absolute;
                        right: 0px;
                        top: -50px;
                        transition: 0.5s 1.4s;
                        transform: rotateY(-100deg); opacity: 0;
                    }
              
                    h2 {
                        display: inline-block;
                        overflow: hidden;
                        position: relative;
                        margin-bottom: 20px;
              
                      .title {
                        font: normal 200px/1 'Abril Fatface';
                        color: #fff;
                        position: relative;
                        transform: scaleY(1.25);
                        opacity: 0;
                        transition: opacity 0.2s 0.5s;
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%; 
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%;
                        transition: right ease-in-out 1s; 
                      }
                    }
              
                    .p1 {
                      display: inline-block;
                      overflow: hidden; 
                      position: relative;
                      margin-right: 14px;
              
                      .p01 {
                        font: 20px/1 'verdana';
                        color: #fff;
                        position: relative;
                        opacity: 0;
                        transition: opacity 0.2s 0.8s;
                        transform: scaleY(1.2);
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%;
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%; 
                        transition: right ease-in-out 1s 0.3s;
                      }
                    }
              
                    .p2 {
                      display: inline-block;
                      overflow: hidden;
                      position: relative;
                      margin-right: 14px;
              
                      .p02 {
                        font: 20px/1 'verdana';
                        color: #fff;
                        position: relative;
                        opacity: 0;
                        transition: opacity 0.2s 1.1s;
                        transform: scaleY(1.2);
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%;
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%; 
                        transition: right ease-in-out 1s 0.6s;
                      }
                    }
                }

                &.on {
                    z-index: 3;
              
                    // .slogan {
                    //   &::before {
                    //     transform: rotateY(-30deg); opacity: 1;
                    //   }
                    //   h2, .p1, .p2{
                    //     .title, .p01, .p02 {opacity: 1;}
                    //     &::after {right: 100%;}
                    //   }
                    // }
                    &:nth-of-type(1).mask {
                      -webkit-mask: url(../../../public/img/mask1.png);
                      -webkit-mask-size: 2300% 100%;
                      mask-size: 2300% 100%;
                      animation: mask-play 1.4s steps(22) forwards;
                    }
                    &:nth-of-type(2).mask {
                        -webkit-mask: url(../../../public/img/mask2.png);
                      -webkit-mask-size: 3000% 100%;
                      mask-size: 3000% 100%;
                      animation: mask-play 1.4s steps(29) forwards;
                    }
                    &:nth-of-type(3).mask {
                        -webkit-mask: url(../../../public/img/mask3.png);
                      -webkit-mask-size: 7100% 100%;
                      mask-size: 7100% 100%;
                      animation: mask-play 1.4s steps(70) forwards;
                    }
                    &:nth-of-type(4).mask {
                        -webkit-mask: url(../../../public/img/mask4.png);
                      -webkit-mask-size: 7400% 100%;
                      mask-size: 7400% 100%;
                      animation: mask-play 1.4s steps(73) forwards;
                    }
                  }

                &.lower {
                z-index: 2;
                }
            }
        }
    }
}

@keyframes mask-play {
    0% {-webkit-mask-position: 0% 0%; mask-position: 0% 0%;}
    100% {-webkit-mask-position: 100% 0%; mask-position: 100% 0%;}
  }


  @media screen and(max-width:1559px) {

    #video{
      .explanation07{right: 23.5%;}
  
      >strong{
          font:bold 200px/1 'Orbitron';
          right: 20px;
  
          b{
            font-size: 250px;
            line-height:150px;
          }
          
      }
  
      .inner{
  
          #navi{
  
              h3{
                font:bold 85px/1 'Orbitron';
                left: 55%;
                top: 20%;
              }
  
              ul{
                  width: 100%;
                  position: absolute;
                  top: 65%;
                  left: 0px;
                  transform: translateY(-50%);
                  overflow: hidden;
  
                  li {
                      padding: 50px 75px 50px 50px;
  
                      h2 {
                        font: bold 10px/1 'orbitron';
                      }

                      &.on {
                      
                        h2::after {
                            width: 140px;
                        }
                      }
                  }
              }
          }
  
          .masks{
              width: 80%;
          }
      }
    }
  }
  
  @media screen and(max-width:$tablet) { 

    #video{
      height: 90vh;

      .explanation07{
        right:20%;
  
        .exP7{
            letter-spacing: 1.3px;
            color:rgba(255,255,255,.4);

        &::before{
            height: 65%;
            bottom:55%;
            right:100.3%;
            background: rgba(255,255,255,.3);
            }
  
          &::after{
              bottom:80.5%;
              right:128%;
              }
          }
      }

      >strong{
          font:bold 175px/1 'Orbitron';
  
          b{
            font-size: 225px;
            line-height:160px;
          }
          
      }
  
      .inner{
  
          #navi{

              h3{
                font:bold 70px/1 'Orbitron';
                right:-190px;
                top: 12.5%;
              }
  
              ul{
                  top: 57.5%;
  
                  li {
                      padding: 40px 50px 40px 30px;
  
                      p{
                        overflow: hidden;
                        text-overflow: ellipsis;   
                        display: -webkit-box;
                        -webkit-line-clamp: 1; 
                        -webkit-box-orient: vertical;
                      }

                      &.on {
                      
                        h2::after {
                            width: 130px;
                        }
                      }
                  }
              }
          }
  
          .masks{
              width:90%;
          }
      }
    }
  }
  
  @media screen and (max-width: 899px){

    #video{
      height: 80vh;

      .explanation07{
        right:12%;
      }

      >strong{
          font:bold 140px/1 'Orbitron';
  
          b{
            font-size: 190px;
            line-height:115px;
          }
          
      }
  
      .inner{
  
          #navi{

              h3{
                font:bold 45px/1 'Orbitron';
                right:-100px;
              }
  
              ul{
  
                  li {

                      h2 {font: bold 11px/1 'orbitron';}

                    &.on {
                            transform: scale(1.2); 

                        h2::after {width: 140px;}
                    }
                  }
              }
          }
  
          .masks{
              width:100%;
          }
      }
    }
  
  }

  @media screen and (max-width: 679px){

    #video{
      height: 100vh;

      .explanation07{display: none;}

      >strong{
          font:bold 120px/1 'Orbitron';
          right: 0;
          letter-spacing: 1.8px;
  
          b{
            font-size: 150px;
            line-height:90px;
          }
          
      }
  
      .inner{
        height: 100%;

        #navi{
            width: 100%;
            height: 30vh;
            top:70vh;

            h3{display: none;}

            ul{
                width: 100%;
                top: 0%;
                left: 0px;
                transform: translateY(0%);
                display: flex;
                flex-wrap: wrap;

                li {
                    width: 50%;
                    height: 50%;
                    padding: 50px 30px;

                    h2 {font: bold 11px/1 'orbitron';}


                    &.on {

                        h2::after {
                            width: 140px;
                        }
                    }
                }
            }
        }

        .masks{height: 70vh;}
      }
    }
  }

  @media screen and (max-width: $mobile){ 
      #video{

      >strong{
          font:bold 75px/1 'Orbitron';
          letter-spacing: 1.5px;
          top: -100px;
  
          b{
            font-size: 100px;
            line-height:50px;
          }
      }
  
      .inner{

        #navi{
            height: 50vh;
            top:50vh;

            ul{
                width: 100%;

                li {
                    width: 100%;
                    height: 20%;
                    padding: 40px 30px;
                }
            }
        }
        .masks{height: 50vh;}
      }
    }
  }





