#member{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/infoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding:50px 0px 250px;
    position: relative;
    overflow: hidden;
    
    .explanation09{
        position: absolute;
        left: 350px;
        bottom: 75px;
        width: 200px;
        height: 200px;

        .exP9{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '';
              display: block;
              font-size: 20px;
              color:rgba(255,255,255,.5);
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }

    strong{
        font:bold 200px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 15px;
        text-align: right;
        display: block;
        
        >b{
            font:bold 250px/130px 'Orbitron';
        }
    }

    h2{
        font:bold 85px/1 'Orbitron';
        color:rgba(255,255,255,.8);
        letter-spacing: 7px;
        position: absolute;
        top: 7.5%;
        left: 52.5%;
    }

    >p{
        font:normal 17px/1 'Orbitron';
        color:rgba(255,255,255,.5);
        letter-spacing: 2px;
        position: absolute;
        top: 17.5%;
        left: 52.9%;
    }

    .inner{
        width: 80%;
        position: relative;
        margin: 0 auto;

        .wrap{
            width: 100%;

            .memberMain{
                width: calc((100% - 300px) / 4);
                margin-right: 100px;
                height: 500px;
                float: left;
                border:1px solid rgba(255,255,255,.5);
                position: relative;
                cursor: pointer;
                transition: 1s;

                &:nth-child(1){
                    top: -100px;
                }

                
                &:nth-child(2){
                    top: 0px;
                }
                
                &:nth-child(3){
                    top: 50px;
                }

                &:nth-child(4){
                    margin-right: 0;
                    top: -50px;
                }
            
                &:hover{
                    transform: translateY(-15px);
                    
                    img{
                        filter: saturate(80%);
                    }
                }

                img{
                    width: 100%;
                    height: 415px;
                    object-fit: cover;
                    position: relative;
                    top: -30px;
                    right: -30px;
                    box-shadow: 3px 3px 5px rgba(0,0,0,.9);
                    filter: saturate(0%);
                    transition: 1s;
                }

                h3{
                    font:bold 15px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 2.3px;
                    margin-bottom: 20px;
                    margin-left: 30px;
                    margin-top: -10px;
                }

                p{
                    font:normal 11px/1 'Orbitron';
                    color:rgba(255,255,255,7);
                    letter-spacing: 1.8px;
                    margin-bottom: 15px;
                    margin-left: 30px;
                }

                span{
                    font:normal 10px/1 'Orbitron';
                    color:rgba(255,255,255,7);
                    letter-spacing: 1.8px;
                    margin-left: 30px;
                    display: block;
                }
            }
        }
    }

    >span{
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.01);
        letter-spacing: 10px;
        position: absolute;
        bottom:2.5%;
        right:-2%;
        text-align: right;
    }
}

@media screen and(max-width:1559px) {
    #member{
        padding:50px 20px 250px;
    
        .explanation09{left: 175px;}
    
        strong{
            font:bold 150px/1 'Orbitron';
