            >b{
                font:bold 200px/110px 'Orbitron';
            }
        }
    
        h2{
            font:bold 65px/1 'Orbitron';
            letter-spacing: 5px;
            left: 40%;
        }
    
        >p{
            font:normal 15px/1 'Orbitron';
            letter-spacing: 1.7px;
            top: 15.5%;
            left: 40.3%;
        }
    
        .inner{
            width: 90%;

            .wrap{
    
                .memberMain{
                    width: calc((100% - 150px) / 4);
                    margin-right: 50px;
                    height: 400px;
    
                    &:nth-child(1){
                        top: -75px;
                    }
    
                    
                    &:nth-child(2){
                        top: 25px;
                    }
                    
                    &:nth-child(3){
                        top: 50px;
                    }
    
                    &:nth-child(4){
                        margin-right: 0;
                        top: -30px;
                    }
    
                    img{height: 315px;}
    
                    h3{font:bold 12px/1 'Orbitron';}
    
                    p{font:normal 10px/1 'Orbitron';}
    
                    span{letter-spacing: 1px;}
                }
            }
        }
    
        >span{
            font:bold 150px/1 'Orbitron';
            right:20px;
        }
    }
}

