@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

.department{
    
    figure{
        width: 100%;
        height: 100%;
        position: relative; 
        margin-top: -100px;
        // overflow: hidden;
        // background-image: url(../../../public/img/aboutBg01.jpg);
        // background-image: url(../../../public/img/youtube01.jpg);
        background:#1e2831;
        // background-size: cover;
        // background-position: center bottom;
        // background-repeat: no-repeat
        // ;
        // filter: saturate(50%);
        overflow: hidden;

        img{
            width:100%;
            height:100%;
            object-fit: cover;
            filter:saturate(20%);
        }
        
        .aboutBg{
            font:bold 500px/1 'Orbitron';
            color:rgba(255,255,255,.07);
            letter-spacing: 25px;
            position: relative;
            top: 100px;
            left: 80%;
            transform: translateX(-50%);
            font-style: italic;
            // border:1px solid red;

            strong{
                position: relative;
                top: 200px;
                animation: departAni2 1s linear;
                animation-fill-mode: forwards;

                b{
                    position: relative;
                    top: -125px;
                    color:rgba(255,255,255,.15);
                    text-shadow: 5px 5px 10px rgba(255,255,255,.1);
                }
            }
        }
    
        .aboutContent{
            position: relative;
            top: 17.5%;
            left:27.5%;
            transform: translate(-50%,-50%);
            width: 35%;
            animation: departAni 1s linear;
            animation-fill-mode: forwards;

            h1{
                font:bold 65px/1 'Orbitron';
                color:#fff;
                letter-spacing: 10px;
                margin-bottom: 20px;
                text-shadow: 5px 5px 7px rgba(0,0,0,.5);
            }

            button{
                width: 170px;
                height: 45px;
                background: #111;
                font:normal 11px/1 'Orbitron';
                color:#fff;
                letter-spacing: 1.2px;
                margin-bottom: 100px;
                cursor: pointer;
            }
        
            b{
                display: block;
                font:bold 12px/1 'Orbitron';
                color:#eee;
                letter-spacing: 1.7px;
                transform: rotate(35deg);
            }
        }

        .aboutList{
            position: absolute;
            top: 18s.5%;
            left: 5.5%;
            transform: translate(-50%,-50%);
            // border:1px solid red;

            >ul{

                li{
                    font:normal 30px/35px 'Orbitron';
                    color:#fff;
                    letter-spacing: 2.3px;
                    text-align: center;
                    margin-bottom: 45px;

                    >b{
                        font:bold 75px/1 'Orbitron';
                        color:#fff;
                        letter-spacing: 3px;
                    }
                }
            }
        }

        .aboutUs{
            width: 100%;
            height: 100%;
            position: relative;
            top:50%;
            margin-bottom: 150px;
            padding:175px 30px 0px 150px;
            display: flex;
            flex-direction: row;

            .custom-shape-divider-bottom-1646653739 {
                position: absolute;
                top: -550px;
                left: 0;
                width: 100%;
                overflow: hidden;
                line-height: 0;
                transform: rotate(177.5deg);
            }
            
            .custom-shape-divider-bottom-1646653739 svg {
                position: relative;
                display: block;
                width: calc(126% + 1.3px);
                height: 500px;
            }
            
            .custom-shape-divider-bottom-1646653739 .shape-fill {
                fill: #2C5973;
                opacity: 0.3;
            }


            .custom-shape-divider-top-1646652967 {
                position: absolute;
                top: -50px;
                left: 0;
                width: 100%;
                overflow: hidden;
                line-height: 0;
                transform: rotate(-2.5deg);
            }
            
            .custom-shape-divider-top-1646652967 svg {
                position: relative;
                display: block;
                width: calc(126% + 1.3px);
                height: 500px;
            }
            
            .custom-shape-divider-top-1646652967 .shape-fill {
                fill: #2C5973;
                opacity: 0.3;
            }

            h2{
                position: absolute;
                top: 100px;
                left:50%;
                transform: translateX(-50%);
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;

                &:before{
                    content: '';
                    display: block;
                    position: absolute;
                    width: 40%;
                    height: 2px;
                    background: #fff;
                    top: -25px;
                    left: -50px;
                }
            }

            .aboutCube{
                position: absolute;
                top: 225px;
                right: 50px;
                width: 50%;
                height:700px;
                perspective: 700px;
                z-index:5;
                
                .wrap{
                    width: 21%;
                    height: 28.5%;
                    position: relative;
                    margin: 0 auto;
                    transform-style: preserve-3d;
                    animation: qnaAni linear 15s infinite;
                    top: 100px;
                    transition: 1s;

                    &:hover{
                        animation-play-state: paused;
       
                        div{
                            opacity: 1;
                            cursor: pointer;
                        }
                    }

                    div{
                        width: 100%;
                        height: 100%;
                        background-image: url(../../../public/img/galleryMain02.jpg);
                        background-repeat: no-repeat;
                        background-size: cover;
                        background-position: center;
                        position: absolute;
                        opacity: 0.7;
                        transition: 1s;
                        filter: saturate(50%);

                        &:nth-child(1){
                            transform: rotateY(0) translateZ(100px);
                        }

                        &:nth-child(2){
                            transform: rotateY(90deg) translateZ(100px);
                            background-image: url(../../../public/img/SERVICE01.jpeg);
                        }

                        &:nth-child(3){
                            transform: rotateY(180deg) translateZ(100px);
                            background-image: url(../../../public/img/SERVICE02.jpg);
                        }

                        &:nth-child(4){
                            transform: rotateY(270deg) translateZ(100px);
                            background-image: url(../../../public/img/SERVICE04.jpg);
                        }

                        &:nth-child(5){
                            transform: rotateX(90deg) translateZ(100px);
                            background-image: url(../../../public/img/galleryMain03.jpg);
                        }

                        &:nth-child(6){
                            transform: rotateX(270deg) translateZ(100px);
                            background-image: url(../../../public/img/galleryMain01.jpg);
                        }
                    }
                }

            }

            .content{
                width: 40%;
                padding:150px 0px 100px;

                img{
                    object-fit: cover;
                    width:100%;
                    height: 350px;
                    filter: saturate(70%);
                    margin-bottom: 30px;
                }
                
                h3{
                    font:bold 35px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 3.3px;
                    margin-bottom: 30px;
                }

                >p{
                    font:normal 12px/25px 'Orbitron';
                    color:#ddd;
                    letter-spacing: 1.5px;
                    margin-bottom: 15px;
                }

                span{
                    font:normal 10px/1 'Orbitron';
                    color:#ccc;
                    letter-spacing: 1.5px;
                    margin-bottom: 25px;
                    display: block;
                }

                b{
                    text-align: right;
                    display: block;
                    font:bold 20px/1'Dancing Script';
                    color:#eee;
                    letter-spacing: 3px;
                }
            }

            .aboutPic{
                width: 60%;
                position: relative;
                padding:150px 30px 100px;

            &::before{
                content: '';
                display: block;
                width: 1.5px;
                height: 80%;
                background: rgba(255,255,255,.7);
                position: absolute;
                left: 100px;
                bottom: 50px;
            }

                strong{
                    position: absolute;
                    top: 525px;
                    font:bold 30px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 3.3px;
                    width: 100%;
                    left: 30%;
                }

                .content{
                    display: flex;
                    position: absolute;
                    top: 450px;
                    left: 15%;
                    width:85%;

                    p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.5);
                        letter-spacing: 1.8px;
                        margin-right: 30px;
                        margin-bottom: 30px;

                        &:nth-of-type(2){
                            margin-right: 0;
                        }
                    }

                    span{
                        font:bold 10px/15px 'Orbitron';
                        color:rgba(255,255,255,.5);
                        letter-spacing: 1.8px;
                    }

                    b{
                        font:bold 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.5);
                        letter-spacing: 1.8px;
                        text-align: left;
                    }
                }
            }
        }

        .aboutService{
            width: 100%;
            position: relative;
            max-height: 700px;
            padding:100px 0px 0px;
            overflow: hidden;

            h2{
                position: absolute;
                top:-10px;
                left:10px;
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
            }

            .swiper{

                .swiper-button-prev{
                    transition: 0.5s;
                    color:#fff;

                    &:hover{
                        color:#111;
                    }

                    &::after{
                        content: '';
                    }
                }

                .swiper-button-next{
                    color:#fff;
                    transition: 0.5s;

                    &:hover{
                        color:#111;

                    }

                    &::after{
                        content: '';
                    }
                }

                .swiper-wrapper{
                    .swiper-slide{
                        display: flex;
                        
                        .servicePic{
                            width: 40%;
                            
                            img{
                                width: 100%;
                                height: 500px;
                                object-fit: cover;
                                filter: saturate(60%);
                            }
                        }
            
                        .serviceContent{
                            width: 60%;
                            background: rgba(0,0,0,.3);
                            padding:0px 50px 0px  250px;
                            
                            b{
                                font:bold 50px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                margin-bottom: 30px;
                                margin-top: 50px;
                                display: block;
                            }
            
                            p{
                                font:normal 10px/20px 'Orbitron';
                                color:rgba(255,255,255,.7);
                                letter-spacing: 2.3px;
                                 margin-bottom: 20px;
                            }

                            span{
                                font:bold 10px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                letter-spacing: 3px;
                                display: block;
                                margin-top:50px;
                                margin-bottom: 30px;
                                text-align: right;
                            }

                            button{
                                font:bold 11px/1 'Orbitron';
                                letter-spacing: 2.3px;
                                width: 130px;
                                height: 35px;
                                padding: 10px;
                                border:none;
                                background: rgba(0,0,0,.3);
                                color:rgba(255,255,255,.7);
                                cursor: pointer;
                                transition: 0.5s;

                                &:hover{
                                    background: rgba(0,0,0,.4);
                                    color:rgba(255,255,255,.9);
                                }
                            }
                        }
                    }
                }
            }
        }

        .etc{
            width: 100%;
            height: 100%;
            position: relative;
            top: 0;
            margin: 0 auto;
            margin-bottom: 100px;
            padding:0px 100px;

            >ul{
                display: flex;
                flex-direction: row;
                justify-content: space-between;

                li{
                    padding:50px 15px 50px 50px;
                    text-align: center;
                    position: relative;

                    &:nth-child(1){
                        &::after{
                            display: none;
                        }
                    }
                    
                    &::after{
                        content: '';
                        display: block;
                        width: 50px;
                        position: absolute;
                        height: 1px;
                        background: #fff;
                        position: absolute;
                        top: 20%;
                        left: 0;
                    }

                    .svg-inline--fa{
                        font-size: 30px;
                        color:rgba(255,255,255,.6);
                        margin-bottom: 30px;
                        padding:10px;
                        border:1px solid #eee;
                        border-radius: 50%;
                        cursor: pointer;
                        transition: 0.5s;

                        &:hover{
                            color:rgba(255,255,255,.9);
                            transform: scale(1.1);
                            box-shadow: 3px 3px 5px rgba(255,255,255,.1);
                        }
                    }

                    span{
                        font:bold 11px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 3px;
                        line-height:15px;
                        margin-bottom: 20px;
                        display: block;

                        b{
                            font:bold 15px/1 'Orbitron';
                            color:rgba(255,255,255,.9);
                            letter-spacing: 3px;
                            margin-right: 10px;
                        }
                    }

                    >p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 2.3px;
                    }
                }
            }
        }

        .inner2{
            width: 100%;
            height: 100%;
            position: relative;
            margin: 0 auto;
            padding-bottom: 50px;
            margin-bottom: 50px;

            h2{
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
                text-align: center;
                margin-bottom: 100px;
            }

            .memberIntro{
                width: 1180px;
                margin: 0 auto;
                display: flex;
                justify-content:space-between;
                align-items: center;
                flex-wrap: wrap;

                >article{
                    width: 30%;
                    height: 500px;
                    padding:15px 15px 100px;
                    background: rgba(255,255,255,.03);
                    box-shadow: 5px 5px 10px rgba(0,0,0,.9);
                    margin-bottom: 100px;
                    transition: 1s;

                    &:hover{
                        background: #fff;
                        transform: scale(1.1);
                        transform: translateY(-30px);
                        
                        h3,p,div{
                            color:#111;
                        }

                        img{
                            filter: saturate(100%);
                        }
                    }

                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        margin-bottom: 15px;
                    }
                    
                    >h3{
                        text-align: center;
                        font:bold 11px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 2.3px;
                        margin-bottom: 15px;
                    }

                    p{
                        text-align: center;
                        font:bold 10px/1 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 5px;
                    }

                    >div{
                        display: flex;
                        width: 50%;
                        margin: 0 auto;
                        justify-content: space-evenly;
                        align-items: center;
                        margin-top: 20px;
                        font-size: 20px;
                        color:rgba(255,255,255,.5);
                    }
                }
            }

        }
    }
}

@keyframes qnaAni{
    0%{transform: rotateY(0deg) rotateX(0deg);}
    100%{transform: rotateY(360deg) rotateX(360deg);}
}

@keyframes departAni{
    0%{transform: translateX(-100%); opacity: 0;}
    100%{transform: translateX(-50%); opacity: 1;}
}

@keyframes departAni2{
    0%{transform: translateX(100%); opacity: 0;}
    100%{transform: translateX(0); opacity: 1;}
}


